body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navigation {
  height: 100%;
  width: 10%;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: #111;
  overflow-x: hidden;
  padding-top: 20px;
}

.navigation a {
  margin-top: 50%;
  padding: 6px 8px 6px 16px;
  text-decoration: none;
  font-size: 25px;
  color: #f1f1f1;
  display: block;
}

.navigation a:hover {
  color: #818181;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
  margin-left: 5%;
}

.container h1 {
  font-family: Tahoma, Geneva, sans-serif;
}
.container .game {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
  padding-top: 2%;
  margin-bottom: 30%;
}

.container .small {
  padding-top: 10%;;
}

 .grid-container {
  box-sizing: border-box;
  border: 1px solid black;
  background-color: lightseagreen;
}
 .row {
  width: 100%;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
}
 .dead-cell {
  width: 20px;
  height: 20px;
  border: 1px solid black;
}
 .live-cell {
  width: 20px;
  height: 20px;
  border: 1px solid black;
  background: white;
}

.controls {
  display: flex;
  width: 100%;
  justify-content: space-around;
  margin-top: 40px;
}

.options {
  background: rgba(0, 0, 0, .6);
  border: 3px white solid;
  margin-top: 15%;
  margin-bottom: 30%;
  height: 100%;
  width: 40%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 5%;
}

.options h1 {
  margin-bottom: 10%; 
}
.options .option-settings {
  display: flex;
  justify-content: space-evenly;
}

.options .option-labels {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  font-size: 1.4em;
}

.options .option-controls {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  margin-bottom: 1%;
}

.options button {
  background: rgba(0, 0, 0, 0);
  color: #fff;
  font-family: Sans-serif;
  font-size: 20px;
  height: 3%;
  width: 150px;
  line-height: 50px;
  margin: 25px 25px;
  text-align: center;
  border: 1px white solid;
  -webkit-transition: all 0.3s ease 0s;
  transition: all 0.3s ease 0s;
}

.size-options button.selected {
  background: rgba(0, 0, 0, .8);
  color: white;
}
.option-controls .size-options .selected button  {
  background-color: pink;
  color: blue;
  font-family: Sans-serif;
  font-size: 20px;
  height: 3%;
  width: 150px;
  line-height: 50px;
  margin: 25px 25px;
  border: 20px red solid;
}

.option-controls .dropdowns {
  display: flex;
  flex-direction: column;
  margin-left: 20%;
}
.option-controls select {
  width: 50%;
  height: 40px;
  color: white;
  position: relative;
  margin-bottom: 4%;
  margin-left: 30px;
  background: transparent;
  border: 1px white solid;
  font-family: Sans-serif;
  font-size: 20px;
  width: 150px;
  line-height: 50px;
  margin: 25px 25px;
}

.option-controls select option {
  background: rgba(0, 0, 0, 1);
}

.option-controls select option:hover {
  color: purple;
}

.rules {
  display: flex;
  flex-direction: column;
  background-color: rgba(0, 0, 0, .6);
  border: 3px white solid;
  padding: 3%;
  font-size: 1.4rem;
  margin-bottom: 30%;
}

.about {
  display: flex;
  flex-direction: column;
  width: 50%;
  background-color: rgba(0, 0, 0, .6);
  border: 3px white solid;
  padding: 3%;
  font-size: 1.4rem;
  margin-bottom: 30%;
}
.App {
  text-align: center;
  position: static;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.background {
  background-image: url("https://i.pinimg.com/originals/c1/e7/30/c1e730a38f30b04816de325e5fced5f8.jpg");
  top: 0;
  width: 100%;
  height: 100%;
  padding-top: 0;
  border: 2px grey solid;
  position: fixed;
  background-attachment: fixed;
  z-index: -100;
}

